@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
.pageHome__card {
  text-decoration: none;
  border-radius: 1em;
  box-shadow: 5px 5px 16px #131313, -5px -5px 16px #393939;
  padding: 2em 0;
  display: flex;
  flex-direction: column;
}

.card__title {
  text-align: center;
  font-family: "Barlow", sans-serif;
  color: #ffffff;
  font-size: 2em;
  font-weight: 600;
  margin: 0 0 0.5em 0;
}

.card__description {
  font-family: "Barlow", sans-serif;
  color: #faf3b0;
}

.pageHome {
  width: 100vw;
  height: 92vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.cardAsistant__image {
  width: 300px;
}
.cardAsistant__name {
  margin: 1em 0;
  text-align: center;
  font-family: "Barlow", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: 1.5px;
}

.today {
  width: 100vw;
  height: 92vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header {
  padding: 1em 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0.5em 0.8em;
  align-items: center;
  box-shadow: 5px 5px 10px #1e1e1e, -5px -5px 10px #2e2e2e;
}

.fa-chevron-left {
  color: #fff;
  font-size: 2.5em;
  text-shadow: 1px 2px 5px #fff;
}

.header__targetAsistant {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__targetAsistant--image {
  border: 1px dashed #fff;
  border-radius: 50%;
  object-fit: cover;
  width: 50px;
}

.header__targetAsistant--name {
  margin: 0.5em 0;
  font-family: "Barlow", sans-serif;
  color: #fff;
  font-weight: 600;
  font-size: 1.3em;
  letter-spacing: 1px;
}

.chat {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.form {
  width: 100%;
  padding: 0.5em;
  background-color: #fff;
  box-shadow: 5px 5px 10px #1e1e1e, -5px -5px 10px #2e2e2e;
}

.form__inputText {
  width: 80%;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 1.2em;
  font-family: "Barlow", sans-serif;
  letter-spacing: -1px;
}

.form__inputSubmit {
  width: 20%;
  font-size: 1.3em;
  border: none;
  outline: none;
  font-weight: 900;
  background-color: #b375c0;
  border-radius: 0.2em;
}

.chat__card--asistant {
  margin: 0.5em 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #fbc2c4;
  padding: 0.5em;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  font-family: "Barlow", sans-serif;
  border-top-left-radius: 1em;
  border-bottom-right-radius: 1em;
  font-size: 1.1em;
  font-weight: 500;
  transition: all 1s ease-out;
  list-style: none;
}

.chat {
  height: 80vh;
  overflow: auto;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #262626;
}

